import React from 'react'
import { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import {AiFillSetting} from 'react-icons/ai'
const ToleranceModal = (props) => {
  const [active,setActive] = useState("one");
  const [tolracneSlip, setTolranceSlip] = useState(0.10)
  const setTolranceValue = (activeStr, value) =>{
    setActive(activeStr);
    setTolranceSlip(value)
  }
  return (
    <div>
           <Modal show={props.show} onHide={() => props.onHide()} animation={false} className="main_modal" >
        <Modal.Header closeButton>
          <Modal.Title className="set_text">Settings</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>Slippage Tolerance</h5>
          <div className='mt-3 d-flex gap-2' >
            <button className={active == "one" ?"btn_tole active":"btn_tole"} onClick={()=>setTolranceValue("one", 0.10)}>0.1%</button>
            <button className={active == "two"?"btn_tole active":"btn_tole"} onClick={()=>setTolranceValue("two", 0.50)}>0.5%</button>
            <button className={active == "three"?"btn_tole active":"btn_tole"} onClick={()=>setTolranceValue("three", 1.00)}>1.0%</button>
          <input className='btn_tole inp' type="number" readOnly value={tolracneSlip == 1 ? tolracneSlip.toFixed(2) : tolracneSlip} /> <span className='mt-2' style={{color:"white"}}>%</span>
          </div>
          
        </Modal.Body>
       
      </Modal>
    </div>
  )
}

export default ToleranceModal
