import React from 'react'
import Modal from 'react-bootstrap/Modal';
import {AiFillSetting} from 'react-icons/ai'
const SettingModal = (props) => {
    

   
  return (
    <div>
         <Modal show={props.show} onHide={() => props.onHide()} animation={false} className="main_modal" >
        <Modal.Header closeButton>
          <Modal.Title className="set_text">Settings</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row justify-content-between d-flex">
          <h5 className="text-primary">GLOBAL</h5>
            <div className="col-md-9 col-9">
              
               <h5 className="mt-3">Light Mode</h5>
              <h5 className="mt-5">Subgraph Health Indicator</h5>
              <h5 className="mt-5">Show username</h5>
              <h5 className="mt-5">Token Risk Scanning</h5>
              <h5 className="mt-5">Default Transaction Speed (GWEI)</h5>
            </div>
            <div className="col-md-3 col-3 mt-2 ms-auto text-end">
          <div className="form-check form-switch switch ms-auto p-0">
  <input className="form-check-input change ms-auto text-end light_mode"  onClick={()=>props.toggleThem()} type="checkbox" role="switch" id="flexSwitchCheckChecked" />
</div>
              </div>
          </div>
        </Modal.Body>
       
      </Modal>
    </div>
  )
}

export default SettingModal
