import React, { useState } from 'react'
import BnbLogo from "../../../Assets/coins/bnb-logo.png";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { useDispatch, useSelector } from 'react-redux';
import { setTokenBValues } from "../../../Redux/selectTokenASlice/selectTokenB";
import { nativeCurrency } from '../../../utils';

const TokenBModal = (props) => {
  let { tokenA } = useSelector((state) => state.selectTokenA);
  let { tokenB } = useSelector((state) => state.selectTokenB);
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const dispatch = useDispatch()
  let [data, setData] = useState([
    {
      coin: nativeCurrency,
      // pic: `BnbLogo`,
      address: "",
      isImport: true
    }, {
      coin: "WBNB",
      // pic: BnbLogo,
      address: "0xae13d989daC2f0dEbFf460aC112a837C89BAa7cd",
      isImport: true
    },
    {
      coin: "Arbi",
      // pic: BnbLogo,
      address: "0xCd39c71F863F1B4A390817ACC7Ea66F9b262767f",
      isImport: true
    },
    {
      coin: "Virtual",
      // pic: BnbLogo,
      address: "0xa718bC2727bEB640E7200d620852cD83140c234C",
      isImport: true
    },
    {
      coin: "BL",
      pic: '',
      address: "0x208F521710620d417E9f35a37f107e360f4A7c3d",

    }
  ])
  return (
    <div>
      <div>
        {/* <Button
                      className="bg-dark dropdown-toggle main_btn"
                      variant="primary"
                      onClick={handleShow}
                    >
                      BNB
                    </Button> */}
        <Modal show={show} onHide={() => props.onHide()}
          {...props}

        >
          <Modal.Header closeButton></Modal.Header>
          <Modal.Title className="text-center fs-3">
            Select a Token
          </Modal.Title>
          <Modal.Body>
            {/* <h5 className="text-center">Specify deposit <span className="fw-100"> TRX </span>amount here:</h5> */}
            <input
              type="text"
              className=" w-100 modal_input mt-1"
              placeholder="Search name or paste address"
            />

            <p className="mt-2">Common Bases</p>
            <div className="d-flex w-100 justify-content-between">
              <button className="btn btn-success">BNB</button>
              <button className="btn btn-success">USDT</button>
              <button className="btn btn-success">CAKE</button>
              <button className="btn btn-success">BTCB</button>
            </div>
            {data.map((item) => {
              return (
                <>
                  <div className="d-flex mt-3 overY" onClick={() => { item.coin == tokenA.coin ? props.onHide() : dispatch(setTokenBValues(item)); props.onHide() }} style={{ cursor: item.coin == tokenA.coin ? "no-drop" : "pointer" }}>
                    <img
                      src={BnbLogo}
                      className="img-fluid mt-2 bnb_img"
                      alt=""
                    />
                    <div className="list ms-2 mt-2">
                      <h5 className="mb-0">{item?.coin}</h5>
                      <p className="mb-0">Binance Chain Native Token</p>
                    </div>
                  </div>
                </>

              )
            })}

          </Modal.Body>
          <Modal.Footer></Modal.Footer>
        </Modal>
      </div>
    </div>
  )
}

export default TokenBModal
